<template>
	<view class="login" :style="{'backgroundImage':'url(' + background +')'}">
		<view class="top_item">
			<text style="color: #000000;font-size: 22px;font-weight: bold;">密码登录</text>
			<text style="color: #000000;font-size: 20px;margin-right: 10px">手机号></text>
		</view>
		<view style="width: 100%; height: 500rpx;"></view>
		
		<view class="input-item">
			<view class="title-content">
				<text class="title">账号</text>
			</view>
			<input class="input" placeholder="请输入用户名" style="margin-left: 80rpx;" v-model="user.username"/>
		</view>
		<view class="input-item">
			<view class="title-content">
				<text class="title">密码</text>
			</view>
			<input class="input" placeholder="请输入密码" style="margin-left: 80rpx;" v-model="user.password" :password="openPassword"/>
			<image class="eyes" :src="eyes==true?'../../static/eye_display.png':'../../static/eye_hidden.png'" alt="" @click="passwordType()">
		</view>
		<view class="input-item">
			<view class="title-content"> 
				<text class="title">身份</text>
			</view>
			<radio-group @change="chang">
				<label v-for="item in radioGroup" :key="item">
					<radio :value="item" :checked="item==activeRadio" style="margin-left: 80rpx;"/>{{item}}
				</label> 
			</radio-group>
		</view>
		<view class="input-item">
			<view class="title-content"> 
				<text class="title">验证码</text>
			</view>
			<input class="input" placeholder="请输入验证码" style="margin-left: 50rpx;width: 400rpx;" v-model="user.code"/>
			<image class="code"  :src="'data:image/png;base64,' + code" @click="updateCode()"></image>

		</view>
		
		<view class="button" @click="login">登录</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				code: '',
				openPassword: true,
				eyes: 0,
				// 单选框数据
				activeRadio: '', //存的是单选按钮选中的value值
				radioGroup: ['用户', '管理员'],
				background: this.baseURL + '/image/background/loginbg1.png',
				user:{}
			}
		},
		onLoad() {
			this.updateCode();
			this.getUser();
		},
		methods: {
			filterBase64(codeImages){
				return codeImages.replace(/[\r\n]/g, "");
			},
			updateCode(){
				var that = this;
				uni.request({
					url: this.baseURL + "/login/kaptcha",
					data: this.user,
					header: {
						// 'Content-Type': 'application/x-www-form-urlencoded'
						 'Content-Type': 'application/json' //自定义请求头信息
					},
					method:'GET',//请求方式，必须为大写
					success: (res) => {
						console.log('接口返回------',res);
						if(res.data.code == 1){
							this.code = that.filterBase64(res.data.data);
						}else{
							uni.showToast({
								title: res.data.msg,
								icon:'none',
								duration: 2000
							});
						}
					}
				})
			},
			getUser(){
				var that = this;
				uni.getStorage({
				    key:"user",
				    success: function(res){
				        console.log(res.data); //user
						that.user = res.data;
				    }
				})
				uni.getStorage({
				    key:"activeRadio",
				    success: function(res){
				        console.log(res.data); 
						that.activeRadio = res.data;
				    }
				})
			},
			passwordType(){
				this.eyes = this.eyes == 0 ? 1 : 0;
				if(this.eyes == 0){
					this.openPassword = true;
				}else{
					this.openPassword = false;
				}
			},
			chang(e) {
				this.activeRadio = e.detail.value;  //选中按钮的value值
				console.log(this.activeRadio);
			},
			login() {
				if(this.activeRadio == '用户'){
					uni.request({
						url: this.baseURL + "/login/userLogin",
						data: this.user,
						header: {
							// 'Content-Type': 'application/x-www-form-urlencoded'
							 'Content-Type': 'application/json' //自定义请求头信息
						},
						method:'POST',//请求方式，必须为大写
						success: (res) => {
							console.log('接口返回------',res.data);
							if(res.data.code == 1){
								uni.showToast({
									title: res.data.msg,
									icon:'none',
									duration: 1000
								}); // 这里写要延时执行的代码
								let password = this.user.password;
								
								this.user = res.data.data;
								this.user.password = password;
								//uni.removeStorageSync("user");
								uni.setStorageSync("user",this.user)
								uni.setStorageSync("activeRadio",this.activeRadio)
								
								setTimeout(function() {
									uni.switchTab({
									     url: '../index/index'
									});  
								}, 1000); // 这里的 1000 表示延时的时间，单位是毫
								
							}else{
								uni.showToast({
									title: res.data.msg,
									icon:'none',
									duration: 2000
								});
							}
						}
					})
				}else if(this.activeRadio == '管理员'){
					uni.request({
						url: this.baseURL + "/login/adminLogin",
						data: this.user,
						header: {
							// 'Content-Type': 'application/x-www-form-urlencoded'
							 'Content-Type': 'application/json' //自定义请求头信息
						},
						method:'POST',//请求方式，必须为大写
						success: (res) => {
							console.log('接口返回------',res);
							if(res.data.code == 1){
								uni.showToast({
									title: res.data.msg,
									icon:'none',
									duration: 2000
								}); 
							}else{
								uni.showToast({
									title: res.data.msg,
									icon:'none',
									duration: 2000
								});
							}
						}
					})
				}else{
					uni.showToast({
						title: '请选择用户身份',
						icon:'none',
						duration: 2000
					});
				}
				
			},
		}
	}
</script>

<style lang="scss">
	page{
		background-color: #eee;
		height: 100%;
		margin: 0;
	}
	.login{
		width: 100%;
		height: 100%;
		margin: 0;
		padding: 32rpx 0;
		background-size: cover;
		position: relative;
		box-sizing:border-box;
		color: #d9e1f4;
	}
	.login:after{
	    content: "";
		width: 100%;
		height: auto;
	    position: absolute;
	    background: inherit;
	    filter: blur(5rpx);
	    z-index: 2;
	}
	.logo {
		display: block;
		width: 320rpx;
		height: 320rpx;
		margin: 100rpx auto 0;
	}
	.top_item {
		display: flex;
		margin-top: 50rpx;
		margin-left: 32rpx;
		margin-right: 32rpx;
		justify-content: space-between;
	}

	.input-item {
		display: flex;
		margin-left: 32rpx;
		margin-right: 32rpx;
		height: 50px;
		align-items: center;
		border-bottom: 1px solid #efeff4;
		margin-bottom: 20rpx;

		.title-content {
			display: flex;
			justify-content: flex-start;
			align-items: center;
			.title {
				width: 2000rpx;
				font-size: 18px;
			}
		}

		.input {
			flex: 1;
		}
	}
	
	.button {
		height: 50px;
		line-height: 50px;
		margin-top: 20rpx;
		margin-left: 32rpx;
		margin-right: 32rpx;
		margin-bottom: 20rpx;
		border-radius: 50rpx;
		font-size: 20px;
		background-color: rgba(255, 255, 255, 0.5);
		text-align: center;
	}
	.code{
		width: 118rpx;
		height: 80rpx;
	}
	.eyes{
		width: 60rpx;
		height: 60rpx;
		margin-left: 66rpx;
	}
</style>